

import "./index.scss";

import Foot from "~/components/Foot"
import {history} from "&/history";

import {axios} from "&";
import { Link } from "react-router-dom";


const main = "";
const token = sessionStorage . token;

export default class Home extends Component{
    constructor(){
        super();
        this.state = {
            main :[]
        }
    }


    
    componentDidMount(){
        
        axios.get("/react/getbookTypes",{params:{
            keyword:"玄幻"
        }})
        .then(res=>{
            this.setState({
                main:res.data.result
            })
            console.log(main)
        })  
    }


    gotoLogin=()=>{
        if(token){

            history.push("./mine")
        }else{
            history.push("./login")

        }
    }

    gotoBookCase=()=>{
        history.push("./bookcase")
    }
    gotoSearch=()=>{
        history.push("./search")
    }
    gotoClassify=()=>{
        history.push("./classify")
    }
    gotoSorting=()=>{
        history.push("./sorting")
    }
    gotoBoy=()=>{
        history.push("./boy")
    }
    gotoGirl=()=>{
        history.push("./girl")
    }
  

    render(){
      
        return(
            <div>
                <div className="homeTop">
                    <p className="topLeft">
                        <span className="logo_img">
                           <img src="http://c-shuqi.11222.cn/assets/logo_7a44de2.png" alt=""/>
                        </span>
                        <span className="logo_txt" >
                           <img src="http://c-shuqi.11222.cn/assets/website-name_c08f935.png" alt=""/>
                        </span>
                    </p>
                    <ul className="topNav">

                        <li onClick={this.gotoLogin}>
                            <img src={require("@/assets/images/touxiang.png")} alt="" />
                         </li>
                         <li onClick={this.gotoBookCase}>
                            <img src={require("@/assets/images/bookshelf.png")} alt=""/>
                         </li>
                         <li onClick={this.gotoSearch}>
                         <img src={require("@/assets/images/search.png")} alt=""/>
                        </li>
                    </ul>

                </div>

                <div className="container">
                    <ul className="container_nav">
                    
                            <li onClick={this.gotoClassify} className="navli">
                                <i style={{backgroundPosition: "0 0"}} ></i>
                                <p>分类</p>
                            </li>
                            <li onClick={this.gotoSorting} className="navli" >
                            <i style={{backgroundPosition: "0 -.88rem"}} ></i>
                                <p>排行</p>
                            </li>
                            <li onClick={this.gotoGirl} className="navli" >
                            <i style={{backgroundPosition: "0 -1.76rem"}} ></i>
                                <p>女频</p>
                            </li>
                            <li onClick={this.gotoBoy} className="navli" >
                            <i style={{backgroundPosition: "0 -2.64rem"}} ></i>
                                <p>男频</p>
                            </li>
                            <li onClick={this.gotoBooklist} className="navli" >
                            <i style={{backgroundPosition: "0 -3.52rem"}} ></i>
                                <p>书单</p>
                            </li>
                      
                    </ul>

                </div>
                <div className="adv"> 
                    <img src="http://img-tailor.11222.cn/pm/book/operate/2018020715462184.jpg" alt=""/>
                </div>
                <div >
                    <p style={{background:"#fff" ,width:"100%",height:"0.8rem" ,lineHeight:"0.8rem" ,textIndent:"0.2rem" , color:"#666"  }}>
                    <span style={{display:"inlineBlock" ,background:"#00c98d", width: "0.12rem",height: "0.56rem" ,}}></span>   
                    精品推荐</p>
                    <div>
                    {
                        this.state.main.map((b,i)=>{
                            return(
                                <div key={i} className="books" >

                                <Link to={"/book/"+b._id}  >
                                
                                <div className="book_img"  style={{width:"1.68rem" , height:"2.24rem" }}>
                                     <img  style={{width:"1.68rem" , height:"2.24rem" }} src={b.cover} alt=""/>
                                </div>

                                <div style={{width:"4.86rem", height:"2.25rem"}} className="book_txt">
                                    <p style={{ fontSize:"0.32rem" ,color:"#333" ,height:"0.4rem" ,lineHeight:"0.4rem" }} > {b.title}</p>
                                    <p style={{ fontSize:"0.25rem" ,color:"#999" ,height:"0.4rem" ,lineHeight:"0.4rem"  }} >
                                        {b.author}
                                    </p>
                                    <p style={{ width:"4.86rem",height:"0.72rem", fontSize:"0.25rem" ,color:"#999" , overflow:"hidden" ,lineHeight:"0.38rem"  }}>
                                        {b.desc}
                                    </p>
                                    <p style={{height:"0.6rem" , lineHeight:"0.6rem" , color:"#666"}}>
                                        <span>{b.status==1?"完结":"连载"} ,</span>
                                        <span>{b.tags}</span>
                                    </p>
                                
                                </div>
                                
                                </Link>
                           
                                 </div>
                            )
                        })
                    } 
                    </div>
                </div>


                <Foot></Foot>
            </div>
        )
    }
}

